












export default {
	data() {
		return {
			titleList: [],
			loadMoreText: "加载中...",
			showLoadMore: false,
			page: 1
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中',
		});
		this.getTitleList();
	},
	// 监听页面卸载
	onUnload() {
		this.titleList = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		this.getTitleList();
		
	},
	methods: {
		getTitleList() {
			uni.request({
				url: this.apiUrl + 'article/getArticleList',
				method: 'GET',
				data: {
					page: this.page,
					limit:18,
					cid:12
				},
				success: (result) => {
					if (result.data.code == 0) {
						if (Array.isArray(result.data.data) && result.data.data.length === 0) {
							this.loadMoreText = "没有更多数据了!"
							return;
						}
						
						if( result.data.data.length < 10){
							this.loadMoreText = "没有更多数据了!"
						}
						this.showLoadMore = true;
						this.page++;
						result.data.data.forEach((article) => {
							this.titleList.push(article);
						});
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: function() {
					uni.hideLoading();
				}
		
			});
		}
	}
}
